import React from 'react'
import './Resume.css'
import { Grid, Paper, TextField, Typography } from '@material-ui/core'
import resumeData from '../../utils/resumeData'
import CustomTimeline, { CustomTimelineSeparator } from '../../components/Timeline/Timeline'
import WorkIcon from '@material-ui/icons/Work';
import SchoolIcon from '@material-ui/icons/School';
import TimelineItem from '@material-ui/lab/TimelineItem'
import TimelineContent from '@material-ui/lab/TimelineContent'
import TimelineDot from '@material-ui/lab/TimelineDot';
import CustomButton from '../../components/Button/Button'

const Resume = () => {
    return (
        <>
            {/* About Me */}
            <Grid container className='section pb_45 pt_45'>
                <Grid item className='section_title mb_30'>
                    <span></span>
                    <h6 className='section_title_text'>À PROPOS DE MOI</h6>
                </Grid>
                <Grid item xs={12}>
                    <Typography variant='body2' className='aboutme_text'>{resumeData.about}</Typography>
                </Grid>
            </Grid>

            {/* Education and Experiences */}
            <Grid container className='section pb_45'>
                <Grid item className='section_title mb_30'>
                    <span></span>
                    <h6 className='section_title_text'>EXPÉRIENCES PROFESSIONNELLES ET PARCOURS ACADÉMIQUE</h6>
                </Grid>
                <Grid item xs={12}>
                    <Grid container className='resume_timeline'>
                        {/* Experiences*/}
                        <Grid item sm={12} md={6}>
                            <CustomTimeline title="Expériences professionnelles" icon={<WorkIcon />}>
                                {resumeData.experiences.map(experience =>
                                (
                                    <TimelineItem>
                                        <CustomTimelineSeparator />
                                        <TimelineContent className='timeline_content'>
                                            <Typography className="timeline_title">{experience.title}</Typography>
                                            <Typography variant='caption' className="timeline_date">{experience.date}</Typography>
                                            <Typography variant='body2' className="timeline_description">{experience.description}</Typography>
                                        </TimelineContent>
                                    </TimelineItem>
                                )
                                )}
                            </CustomTimeline>
                        </Grid>
                        {/* Education */}
                        <Grid item sm={12} md={6}>
                            <CustomTimeline title="Parcours académique" icon={<SchoolIcon />}>
                                {resumeData.educations.map(education =>
                                (
                                    <TimelineItem>
                                        <CustomTimelineSeparator />
                                        <TimelineContent className='timeline_content'>
                                            <Typography className="timeline_title">{education.title}</Typography>
                                            <Typography variant='caption' className="timeline_date">{education.date}</Typography>
                                            <Typography variant='body2' className="timeline_description">{education.description}</Typography>
                                        </TimelineContent>
                                    </TimelineItem>
                                )
                                )}
                            </CustomTimeline>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>

            {/* Skills */}
            <Grid container className='section graybg pb_45 p_50'>
                <Grid item className='section_title mb_30'>
                    <span></span>
                    <h6 className='section_title_text'>COMPÉTENCES</h6>
                </Grid>
                <Grid item xs={12}>
                    <Grid container justify="space-between" spacing={3}>
                        {resumeData.skills.map(skill =>
                        (
                            <Grid item xs={12} sm={6} md={3}>
                                <Paper elevation={0} className='skill'>
                                    <Typography variant='h6' className='skill_title'>
                                        {skill.title}
                                    </Typography>
                                    {skill.description.map((element) => (
                                        <Typography variant='body2' className='skill_description'>
                                            <TimelineDot variant='outlined' className='timeline_dot' />
                                            {element}
                                        </Typography>
                                    ))}
                                </Paper>
                            </Grid>
                        )
                        )}
                    </Grid>
                </Grid>

            </Grid>

            {/* Contact */}
            <Grid container spacing={6} className='section pt_45'>
                {/* Contact Form */}
                <Grid item xs={12} lg={7}>
                    <Grid container>
                        <Grid item className='section_title mb_30'>
                            <span></span>
                            <h6 className='section_title_text'>FORMULAIRE DE CONTACT</h6>
                        </Grid>


                        <Grid item xs={12}>
                            <Grid container spacing={3}>
                                <Grid item xs={12} sm={6}>
                                    <TextField fullWidth name='name' label='Nom' />
                                </Grid>
                                <Grid item xs={12} sm={6}>
                                    <TextField fullWidth name='email' label='E-mail' />
                                </Grid>
                                <Grid item xs={12}>
                                    <TextField fullWidth name='message' label='Message' multiline rows={4} />
                                </Grid>
                                <Grid item xs={12}>
                                    <CustomButton><span className='btn_text'>Soumettre</span></CustomButton>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>

                {/* Contact Information */}
                <Grid item xs={12} lg={5}>
                    <Grid container>
                        <Grid item className='section_title mb_30'>
                            <span></span>
                            <h6 className='section_title_text'>INFORMATIONS DE CONTACT</h6>
                        </Grid>

                        <Grid item xs={12}>
                            <Grid container>
                                <Grid item xs={12} >
                                    <Typography className='contactInfo_item'>
                                        <span>Adresse : </span>{resumeData.address}
                                    </Typography>
                                </Grid>
                                <Grid item xs={12} >
                                    <Typography className='contactInfo_item'>
                                        <span>Tél : </span>{resumeData.phone}
                                    </Typography>
                                </Grid>
                                <Grid item xs={12} >
                                    <Typography className='contactInfo_item'>
                                        <span>E-mail : </span>{resumeData.email}
                                    </Typography>
                                </Grid>
                            </Grid>
                        </Grid>

                        <Grid item xs={12}>
                            <Grid container className='contactInfos_socialsContainer'>
                                {Object.keys(resumeData.socials).map((key) => (
                                    <Grid item className='contactInfo_social'>
                                        <a href={resumeData.socials[key].link} target="_blank" rel="noreferrer">
                                            {resumeData.socials[key].icon}
                                        </a>
                                    </Grid>
                                ))}
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>

            </Grid >

        </>
    )
}

export default Resume
