import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Nav, Navbar } from 'react-bootstrap'
import { NavLink } from 'react-router-dom'
import HomeRoundedIcon from '@material-ui/icons/HomeRounded';
import TelegramIcon from '@material-ui/icons/Telegram';
import resumeData from '../../utils/resumeData';
import CustomButton from '../Button/Button';
import './Header.css';
import { withRouter } from '../../utils/withRouter';

const Header = (props) => {
    const pathName = props?.location?.pathname
    return (
        <Navbar expand="lg" sticky='top' className='header'>
            {/* Home Link */}
            <Nav.Link as={NavLink} to="/" className="header_navlink">
                <Navbar.Brand className="header_home">
                    <HomeRoundedIcon />
                </Navbar.Brand>
            </Nav.Link>

            <Navbar.Toggle />

            <Navbar.Collapse>
                <Nav className="header_left">
                    {/* Resume Link */}
                    <Nav.Link
                        as={NavLink}
                        to="/"
                        className={pathName === "/" ? "header_link_active" : "header_link"}>
                        CV
                    </Nav.Link>

                    {/* Portfolio Link */}
                    <Nav.Link
                        as={NavLink}
                        to="/portfolio"
                        className={pathName === "/portfolio" ? "header_link_active" : "header_link"}>
                        Projets
                    </Nav.Link>

                    {/* Certifications Link */}
                    <Nav.Link
                        as={NavLink}
                        to="/certifications"
                        className={pathName === "/certifications" ? "header_link_active" : "header_link"}>
                        Certifications
                    </Nav.Link>
                </Nav>

                <div className='header_right'>
                    {Object.keys(resumeData.socials).map((key) => (
                        <a href={resumeData.socials[key].link} target="_blank" rel="noreferrer">
                            {resumeData.socials[key].icon}</a>))}

                    <CustomButton icon={<TelegramIcon />} >
                        <span className='btn_text'>Recrutez Moi</span>
                    </CustomButton>
                </div>
            </Navbar.Collapse>
        </Navbar>
    )
}

export default withRouter(Header)
